.title {
  color: #4169e1;
}

.nav {
  display: flex;
}

.alert {
  color: #2a2a2a;
  -ms-user-select: none;
  user-select: none;
  cursor: pointer;
  visibility: hidden;
  opacity: 0;
  background-color: #fff;
  border: 1px solid #2a2a2a;
  border-radius: 3px;
  margin: 0;
  padding: 24px;
  font-size: 18px;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  bottom: 20px;
  right: 20px;
  transform: translateX(calc(100% + 20px));
}

.alert.success {
  color: #fff;
  background-color: #4caf50;
  border: 1px solid #388e3c;
}

.alert.is-visible {
  visibility: visible;
  opacity: 1;
  transform: translateX(0);
}

